@import url(https://fonts.googleapis.com/css2?family=Mountains+of+Christmas:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}
.Login img {
    margin: 0 auto;
    -webkit-animation: hat-in 5s normal;
            animation: hat-in 5s normal;
}

.input-group {
    margin: 0 auto;
    width: 500px;
    opacity: 0;
    -webkit-animation: show-input 5s forwards;
            animation: show-input 5s forwards;
}

.login-info {
    margin: '0 auto'
}

.login-info p {
    text-align: center;
    color: #666;
    opacity: 0;
    -webkit-animation: show-text 2s 5s forwards;
            animation: show-text 2s 5s forwards;
}

@-webkit-keyframes hat-in {
    from { -webkit-transform: translateY(-400px); transform: translateY(-400px); }
    to {-webkit-transform: translateY(0px);transform: translateY(0px);}
}

@keyframes hat-in {
    from { -webkit-transform: translateY(-400px); transform: translateY(-400px); }
    to {-webkit-transform: translateY(0px);transform: translateY(0px);}
}

@-webkit-keyframes show-input {
    to { opacity: 1; }
}

@keyframes show-input {
    to { opacity: 1; }
}

@-webkit-keyframes show-text {
    to { opacity: 1; }
}

@keyframes show-text {
    to { opacity: 1; }
}
.santa img {
    height: 75vh;
    position: absolute;
    bottom: 10px;
    left: 10px;
}

header {
    margin-top: 15px;
    margin-left: 15px;
    border: 1px solid silver;
    border-radius: 15px;
    width: 650px;
    padding: 15px;
}

header :nth-child(1) {
    margin-bottom: 0;
    white-space: nowrap;
    overflow: hidden;
    -webkit-animation: santa-text 5s steps(87,end) 1s 1 normal both;
            animation: santa-text 5s steps(87,end) 1s 1 normal both;
}

header :nth-child(2) {
    white-space: nowrap;
    overflow: hidden;
    -webkit-animation: santa-text 5s steps(88,end) 5s 1 normal both;
            animation: santa-text 5s steps(88,end) 5s 1 normal both;
}

header :nth-child(3) {
    margin-bottom: 0;
    margin-top: 20px;
    font-size: 24px;
    white-space: nowrap;
    overflow: hidden;
    opacity: 0;
    -webkit-animation: santa-text-scale 5s 10s ease-out forwards;
            animation: santa-text-scale 5s 10s ease-out forwards;
}

.prezenty-container {
    position: absolute;
    left: 720px;
    right: 0;
    top: 200px;
}

.prezenty {
    margin: 0 auto;
    max-width: 800px;
    opacity: 0;
    -webkit-animation: prezenty 5s 12s ease-out forwards;
            animation: prezenty 5s 12s ease-out forwards;
}

.prezent-duzy {
    width: 250px;
    margin-right: 40px;
    margin-bottom: 40px;
    cursor: pointer;
}

.prezent-duzy:hover {
    -webkit-mask-image: linear-gradient(black, black, transparent);
            mask-image: linear-gradient(black, black, transparent);
}

.prezent-duzy:active {
    -webkit-transform: scale(0.98);
            transform: scale(0.98);
}

