.wybrany-prezent {
    margin: 0 auto;
    text-align: center;
}

.prezent-info {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 18px;
    white-space: pre;
}

.prezent-otwarty-content {
    position: absolute;
    top: 50%;
    left: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    font-family: 'Mountains of Christmas', cursive;
    color: #fff;
}

.prezent-otwarty-content h1 {
    color: gold;
    font-size: 52px;
    margin-bottom: 40px;
}

.prezent-otwarty-content p {
    font-size: 26px;
}

.prezent-otwarty-content small {
    display: block;
    width: 100%;
    text-align: right;
    margin-top: 40px;
}

@-webkit-keyframes santa-text {
    from { width: 0; }
    to { width: 650px; }
}

@keyframes santa-text {
    from { width: 0; }
    to { width: 650px; }
}

@-webkit-keyframes santa-text-scale {
    to { opacity: 1; }
}

@keyframes santa-text-scale {
    to { opacity: 1; }
}

@-webkit-keyframes prezenty {
