}
.wait-info {
    background-color: rgba(51, 51, 51, 0.7);
    width: 450px;
    height: 120px;
    position: absolute;
    top: 20px;
    right: 20px;
    border-radius: 15px;
    font-size: 18px;
}

.wait-info:first-child {
    color: #fff;
    opacity: 0.95;
    padding: 20px 20px 10px 20px;
    text-align: center;
    vertical-align: middle;
    display: flex;
    align-items: center;
    justify-content: center;
}
