.Login img {
    margin: 0 auto;
    animation: hat-in 5s normal;
}

.input-group {
    margin: 0 auto;
    width: 500px;
    opacity: 0;
    animation: show-input 5s forwards;
}

.login-info {
    margin: '0 auto'
}

.login-info p {
    text-align: center;
    color: #666;
    opacity: 0;
    animation: show-text 2s 5s forwards;
}

@keyframes hat-in {
    from { transform: translateY(-400px); }
    to {transform: translateY(0px);}
}

@keyframes show-input {
    to { opacity: 1; }
}

@keyframes show-text {
    to { opacity: 1; }
}