.santa img {
    height: 75vh;
    position: absolute;
    bottom: 10px;
    left: 10px;
}

header {
    margin-top: 15px;
    margin-left: 15px;
    border: 1px solid silver;
    border-radius: 15px;
    width: 650px;
    padding: 15px;
}

header :nth-child(1) {
    margin-bottom: 0;
    white-space: nowrap;
    overflow: hidden;
    animation: santa-text 5s steps(87,end) 1s 1 normal both;
}

header :nth-child(2) {
    white-space: nowrap;
    overflow: hidden;
    animation: santa-text 5s steps(88,end) 5s 1 normal both;
}

header :nth-child(3) {
    margin-bottom: 0;
    margin-top: 20px;
    font-size: 24px;
    white-space: nowrap;
    overflow: hidden;
    opacity: 0;
    animation: santa-text-scale 5s 10s ease-out forwards;
}

.prezenty-container {
    position: absolute;
    left: 720px;
    right: 0;
    top: 200px;
}

.prezenty {
    margin: 0 auto;
    max-width: 800px;
    opacity: 0;
    animation: prezenty 5s 12s ease-out forwards;
}

.prezent-duzy {
    width: 250px;
    margin-right: 40px;
    margin-bottom: 40px;
    cursor: pointer;
}

.prezent-duzy:hover {
    mask-image: linear-gradient(black, black, transparent);
}

.prezent-duzy:active {
    transform: scale(0.98);
}

.prezent-sredni {
    width: 180px;
    margin-right: 40px;
    margin-bottom: 40px;
    cursor: pointer;
}

.prezent-sredni:hover {
    mask-image: linear-gradient(black, black, transparent);
}

.prezent-sredni:active {
    transform: scale(0.98);
}

.prezent-maly {
    width: 140px;
    margin-right: 40px;
    margin-bottom: 40px;
    cursor: pointer;
}

.prezent-maly:active {
    transform: scale(0.98);
}

.prezent-maly:hover {
    mask-image: linear-gradient(black, black, transparent);
}

.prezent-maly:active {
    transform: scale(0.98);
}

.prezent-malutki {
    width: 100px;
    margin-right: 40px;
    margin-bottom: 40px;
    cursor: pointer;
}

.prezent-malutki:hover {
    mask-image: linear-gradient(black, black, transparent);
}

.prezent-malutki:active {
    transform: scale(0.98);
}

.wybrany-prezent {
    margin: 0 auto;
    text-align: center;
}

.prezent-info {
    margin-top: 20px;
    margin-bottom: 20px;
    font-size: 18px;
    white-space: pre;
}

.prezent-otwarty-content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translateY(-50%);
    font-family: 'Mountains of Christmas', cursive;
    color: #fff;
}

.prezent-otwarty-content h1 {
    color: gold;
    font-size: 52px;
    margin-bottom: 40px;
}

.prezent-otwarty-content p {
    font-size: 26px;
}

.prezent-otwarty-content small {
    display: block;
    width: 100%;
    text-align: right;
    margin-top: 40px;
}

@keyframes santa-text {
    from { width: 0; }
    to { width: 650px; }
}

@keyframes santa-text-scale {
    to { opacity: 1; }
}

@keyframes prezenty {
    to { opacity: 1; }
}